.mobile-nav {
  position: fixed;
  z-index: 16;
  left: 0;
  top: 48px;
  height: calc(100% - 48px);
  width: 35%;
  visibility: hidden;
  background-color: #fff;
  overflow-y: auto;
  transform: translateX(-100%);
  transition: transform 0.4s 0s, visibility 0s 0.4s;

  &.lateral-menu-is-open {
    transform: translateX(0);
    visibility: visible;
    transition: transform 0.4s 0s, visibility 0s 0s;
  }
}
